import Nav from "./components/Nav/Nav";
import {BrowserRouter, Route} from "react-router-dom"
import Cards from "./components/Cards/Cards"
import {useState, useEffect} from "react"
import CharacterDetail from "./components/CharacterDetail/CharacterDetail";

function App() {
  
  const [characters, setCharacters] = useState([])
  const [searchCharacters, setSearchCharacters] = useState([])

  
  useEffect(()=> fetch("https://hp-api.herokuapp.com/api/characters")
  .then(r => r.json())
  .then((recurso) => {
      setCharacters(recurso)
  })
, [])

 function searchByName (name) {
  fetch("https://hp-api.herokuapp.com/api/characters")
  .then(r => r.json())
  .then((recurso) => {
      setSearchCharacters(recurso.filter(el => el.name.toUpperCase().includes(name.toUpperCase())))
      
  })
 }
 



  return (
    <div className="App">
      <BrowserRouter>
      <Route path="/" basename={process.env.PUBLIC_URL} render={(props) =><Nav searchByName={searchByName} history={props.history}/>}/>
      <Route exact path="/" basename={process.env.PUBLIC_URL}>
        <Cards characters={characters}/>
      </Route>

      <Route exact path="/search/" basename={process.env.PUBLIC_URL}>
            <Cards characters={searchCharacters}/>
      </Route>
      <Route
            exact path="/character/:name"
            basename={process.env.PUBLIC_URL}
            render={({match}) => <CharacterDetail
            name={match.params.name} characters={characters}/>
            }
          />
      
      
      </BrowserRouter>
    </div>
     )
}

export default App;
